body {
  background-color: #ccc9c9;
}

.search{
  display: block;
  margin: 15px auto;
  max-width: 500px;
  min-width: 200px;
  background-color: #a5d4de;
  color: rgb(4, 4, 80);
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 15px;
  border: 2px solid rgb(19, 14, 95);
  padding: 10px;
}
.search::placeholder{
  all: unset;
  background-color: #a5d4de;
  color: rgb(78, 78, 82);
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

.app{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

